import axios from 'axios'

export default {
  state: {
    account: []
  },
  mutations: {
    setAccount (state, post) {
      state.account = post
    }
  },
  actions: {
    accountPost ({ commit }) {
      axios
        .get('https://api.launchfxm.com/api_v1/user?Login=xxxxxx').then((getResponse) => {
          commit('setAccount', getResponse.data)
        }).catch(e => { return e })
    }
  },
  modules: {
  },
  getters: {
    getAccount: (state) => state.account
  }
}
