import axios from 'axios'
export default {
  namespaced: true,
  state: {
  },
  mutations: {
  },
  actions: {
    sendDeposit (deposit) {
      axios
        .post(`https://api.launchfxm.com/api_v1/account_deposit?Login=xxxxxx&Deposit=${deposit}&Comment=deposittoaccount`)
    },
    withdrawDeposit (deposit) {
      axios
        .post(`https://api.launchfxm.com/api_v1/account_withdraw?Login=xxxxxx&Withdraw=${deposit}&Comment=withdrawfromaccount`)
    }
  },
  getters: {
  }
}
