<template>
  <router-view/>
</template>
<script>
import { core } from './config/pluginInit'
import api from '@/router/api'
export default {
  name: 'App',
  components: {
  },
  mounted () {
    core.mainIndex()
  },
  created () {
    if (localStorage.getItem('reflex')) {
      const token = localStorage.getItem('reflex')
      api
        .post('/users/userLoginControl', {
          token: token
        })
        .then(res => {
          if (Number(res.data.status) === 1) {
            this.$store.dispatch('Setting/authUserAction', {
              auth: true,
              authType: 'email',
              user: {
                id: res.data.user.id,
                identificationNumber: res.data.user.identificationNumber,
                fullName: res.data.user.fullName,
                phone: res.data.user.phone,
                email: res.data.user.email,
                password: res.data.user.password,
                country: res.data.user.country,
                city: res.data.user.city
              }
            })
          } else {
            localStorage.removeItem('reflex')
          }
        })
        .catch(err => {
          return err
        })
    }
  }
}
</script>
<style lang="scss">
  @import "assets/scss/style.scss";
</style>
